  background-image: url("../img/icons/general/mail-green.svg");
  margin-right: 10px;
}

.search {
  display: inline-flex;
  align-items: center;
}

.search:before {
  content: '';
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.search:before {
  width: 20px;
  height: 20px;
  background-image: url("../img/icons/general/search-white.svg");
  margin-right: 10px;
}

.more {
  width: 13px;
  height: 2px;
  background-color: #ffffff;
  border-radius: 4px;
  position: relative;
  margin-right: 5px;
}

.more:before, .more:after {
  content: "";
  background-color: #ffffff;
  height: 2px;
  width: 100%;
  border-radius: 4px;
  display: block;
  position: absolute;
}

.more:before {
  top: -5px;
}

.more:after {
  bottom: -5px;
}

.close {
  width: 13px;
  position: relative;
  margin-right: 5px;
}

.close:before, .close:after {
  content: "";
  border-radius: 4px;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  display: block;
  position: absolute;
  top: -1px;
}

.close:before {
  transform: rotate(45deg);
}

.close:after {
  transform: rotate(-45deg);
}

.custom-object-fit {
  height: 100%;
  background-size: cover;
  background-position: center;
}

.custom-object-fit img {
  display: none;
}

