.icon-button {
  position: relative;
}

.icon-button:hover:after {
  opacity: 1;
  bottom: 120%;
  z-index: 5;
}

.icon-button:after {
  color: #ffffff;
  content: attr(data-content);
  background: #8595A4;
  position: absolute;
  bottom: 140%;
  font-size: 1.3rem;
  transition: all 0.4s;
  transform: translateX(-50%);
  left: 50%;
  padding: 8px;
  z-index: -5;
  opacity: 0;
  line-height: 1.3;
  text-align: center;
  border-radius: 4px;
  width: 200px;
}

@media only screen and (max-width: 959px) {
  .icon-button:after {
    color: #222429;
    content: attr(data-content);
    background: none;
    position: inherit;
    bottom: auto;
    white-space: nowrap;
    left: auto;
    padding: 0;
    opacity: 1;
    line-height: 1.3;
    top: 0px;
    margin-left: 10px;
    z-index: 5;
  }
  .icon-button:hover {
    color: #3DAD5B;
  }
}

